
















  import Vue from 'vue';

  export default Vue.extend({

    name: 'App',

    data() {
      return {
        token: '',
        loggedin: false,
      }
    },

    watch: {
      'token'(to, from) {

        // Log
        console.log('new token found: ' + to)

        // Set the new token
        this.getToken()

        // Check if logged in
        if (this.token.length > 0) {
          this.loggedin = true
        } else {
          this.loggedin = false
        }

      }
    },

    mounted() {
      this.getToken()
    },

    methods: {

      logout() {
        const self: any = this
        self.$cookie.delete('auth');
        this.token = ''
      },

      getToken() {

        const self: any = this

        // Get token from cookie
        var cookie = self.$cookie.get('auth');

        this.token = cookie || ''

      }

    }

  })
